import React, { Component } from 'react'
import './Prezent.css'
import WaitForIt from '../WaitForIt/WaitForIt'
import PrezentWybrany from './PrezentWybrany';

export default class Prezent extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        new Audio('sounds/[Free-scores.com]_preipont-james-jingle-bells-52796.mp3').play();
        this.showList();
    }

    state = {
        widok: 1,
        wybranyPrezent: 'brak'
    }

    showList() {
        this.setState({ wybranyPrezent: <div>
            <img className='prezent-duzy' src='img/prezent.png' alt='prezent-duży' onClick={() => this.showPrezent('350px', 'prezent', 'Taa. Od razu największy...\nUważaj bo jeszcze wpiszę Cię na listę niegrzecznych :-P')} />
            <img className='prezent-sredni' src='img/prezent.png' alt='prezent-średni' onClick={() => this.showPrezent('300px', 'prezent-krzys', 'Trafiłaś na prezent Krzysia, postaraj się.')} />
            <img className='prezent-sredni' src='img/prezent.png' alt='prezent-średni' onClick={() => this.showPrezent('350px', 'prezent-magda', 'Brawo, to twój prezent!\nMożesz go otworzyć ;-)', true)} />
            <img className='prezent-maly' src='img/prezent.png' alt='prezent-mały' onClick={() => this.showPrezent('250px', 'prezent-daria', 'Hej, to prezent dla Darii!')} />
            <img className='prezent-maly' src='img/prezent.png' alt='prezent-mały' onClick={() => this.showPrezent('250px', 'prezent-marcin', 'Wygląda na to, że to prezent Marcina...')} />
            <img className='prezent-sredni' src='img/prezent.png' alt='prezent-średni' onClick={() => this.showPrezent('300px', 'prezent', 'O, a ten nie jest podpisany...\nKtóry elf to pakował???')} />
            <img className='prezent-malutki' src='img/prezent.png' alt='prezent-malutki' onClick={() => this.showPrezent('200px', 'prezent', 'Już nie tak skromnie...\nTwój prezent jest większy :-)')} />
        </div> });
    }

    showPrezent(rozmiar, img, info, magdy = false) {
        this.setState({ wybranyPrezent: <PrezentWybrany rozmiar={rozmiar} img={img} info={info} go={ magdy ? this.showPrezentMagdy.bind(this) : this.showList.bind(this) } prezentMagdy={magdy} /> });
    }

    showPrezentMagdy() {
        this.props.open();
    }

    render() {
        let prezent = this.state.wybranyPrezent;

        return (
            <div>
                <header>
                    <p>Witaj w świecie <i><b>Tajnego Mikołaja</b></i>. W tym świecie spełniają się wszystkie marzenia.</p>
                    <p>Z okazji tegorocznych świąt przygotowałem sporo prezentów, spróbuj odszukać swój.</p>
                    <p>Powodzenia!</p>
                </header>
                <div className='santa'>
                    <img src='img/santa.png' alt='' />
                </div>
                <div className='prezenty-container'>
                    <div className='prezenty'>
                        {prezent}
                    </div>
                </div>
            </div>
        )
    }
}
