import React, { Component } from 'react'

export default class PrezentWybrany extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const { rozmiar, img, info, go, prezentMagdy } = this.props;

        const goNext = e => {
            e.preventDefault();
            go();
        }

        return (
            <div className='wybrany-prezent'>
                <img src={'img/' + img +'.png'} alt='prezent' style={{ width: rozmiar }} />
                <div className='prezent-info'>
                    {info}
                </div>
                <button onClick={ goNext } className='btn btn-success'>{prezentMagdy ? "Otwórz" : "Spróbuj jeszcze raz"}</button>
            </div>
        )
    }
}
