import React, { Component } from 'react'

export default class PrezentOtwarty extends Component {
    render() {
        return (
            <div className='prezent-otwarty-content'>
                <h1>WESOŁYCH ŚWIĄT</h1>
                <p>
                    Świąt wesołych i szalonych <br />
                    pełnych uciech i uśmiechu <br />
                </p>
                <p>
                    Niechaj w życiu twym zagości <br />
                    garść białego jak śnieg... śniegu :)
                </p>
                <p>
                    Karp niech nie ma ości wcale <br />
                    by się jadło go wspaniale
                </p>
                <p>
                    A ten koleś, ten z saniami <br />
                    niechże sypnie prezentami
                </p>
                <p>
                    I umili świąt tych czas <br />
                    Covid niech se pójdzie w las ;)
                </p>
                <small>
                    TWÓJ TAJNY MIKOŁAJ
                </small>
            </div>
        )
    }
}

