import React, { Component } from 'react'
import './App.css';
import Login from './Login/Login';
import Prezent from './Prezent/Prezent';
import PrezentOtwarty from './Prezent/PrezentOtwarty';
import WaitForIt from './WaitForIt/WaitForIt';

export default class App extends Component {

  componentDidMount() {
    this.checkDate();
  }  

  state = {
    loggedIn: false,
    isValidDate: false,
    opened: false
  }

  logIn = () => {
    this.setState({ loggedIn: true });
  }

  open = () => {
    this.setState({ opened: true });
  }

  async checkDate() {
    var response = await fetch("https://kowalec.net/tajny-mikolaj-2021/time.php");
    var json = await response.json();

    var current = new Date(json.date);
    console.log(current);
    var expected = new Date('2021-12-20T10:00:00');

    this.setState({ isValidDate: current >= expected });
  }

  render() {
    const { loggedIn, isValidDate, opened } = this.state;
    document.body.style.background = 'none';

    if(!isValidDate) {
      document.body.style.backgroundImage = "url('http://source.unsplash.com/1600x900?christmas')";
      document.body.style.backgroundSize = "cover";
      document.body.style.backgroundRepeat = "no-repeat";

      return <WaitForIt />
    }

    if(opened) {
      document.body.style.backgroundImage = "url('img/prezent-back.jpg')";
      document.body.style.backgroundSize = "cover";
      document.body.style.backgroundRepeat = "no-repeat";
      return <PrezentOtwarty />
    }
    else if(loggedIn) {
      return <Prezent open={ this.open } />
    } else {
      return <Login logIn={ this.logIn } />
    }
  }
}
