import './Login.css';
import React, {useState} from 'react';

const Login = ({ logIn }) => {
    const [state, setState] = useState({
        email: "",
        emailInfo: ""
    });

    const handleInput = e => {
        const value = e.target.value;
        setState({ email: value });
    }

    const TryLogIn = e => {
        e.preventDefault();
        if(!state.email) {
            setState({emailInfo: "Wprowadź adres email"});
        }
        else if(!/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w+)+$/.test(state.email)) {
            setState({emailInfo: "To nie jest poprawny adres email"});
        }
        else {
            fetch(`https://kowalec.net/tajny-mikolaj-2021/email.php?email=${state.email}`)
                .then(data => data.json())
                .then(json => {
                    if(json.status) logIn();
                    else setState({emailInfo: json.info});
                });
        }
    }

    return (
      <div className="Login">
        <img src="img/czapka.png" alt="" style={{ display: 'block' }} />
        <div className="input-group mb-3">
            <div className="input-group-prepend">
                <label className="input-group-text" htmlFor='email'>Adres email</label>
            </div>
            <input type="email" className={`form-control ${state.emailInfo ? "is-invalid" : ""}`} name="email" placeholder='moj.adres@email.pl' onChange={ handleInput } />
            <button type="button" className='btn btn-primary px-3' onClick={ TryLogIn }><i className="bi bi-gift-fill"></i></button>
            <div className='invalid-feedback'>
                {state.emailInfo}
            </div>
        </div>
        
        <div className='login-info'>
            <p>
                Przedstaw się swojemu tajnemu mikołajowi używając
                <br />
                swojego adresu email, aby otrzymać swój prezent.
            </p>
        </div>
      </div>
    );
  }
  
  export default Login;